<template>
  <div id="maphome">
    <Tabs value="name1" style="margin:30px;color:white">
      <Tab-pane label="高德地图" name="name1" icon="map">
        <div id="mapcontent">
          <div id="gaodeMap"></div>
        </div>
      </Tab-pane>
      <Tab-pane label="腾讯地图" name="name2" icon="map">
        <div id="mapcontent">
          <div id="tencentMap"></div>
        </div>
      </Tab-pane>
      <Tab-pane label="GoogleMap" name="name3" icon="map">
        <div id="mapcontent">
          <div id="googleMap"></div>
        </div>
      </Tab-pane>
      <Tab-pane label="OpenStreetMap" name="name4" icon="map">
        <div id="mapcontent">
          <div id="openMap"></div>
        </div>
      </Tab-pane>
    </Tabs>
  </div>
</template>

<script>
  export default {
    name: 'mapUseView',
    data() {
      return {

      }
    },
    computed: {},
    mounted() {
      this.initGaode();
      this.initTencent();
      this.initGoogle();
      this.initOpen();
    },
    watch: {},
    methods: {
      initGaode() {
        var gaodeMap = L.map('gaodeMap', {
          center: [39.916042, 116.390694],
          zoom: 14,
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        L.tileLayer('http://webrd0{s}.is.autonavi.com/appmaptile?lang=zh_cn&size=1&scale=1&style=8&x={x}&y={y}&z={z}', {
          attribution: '铿锵侠 &copy; 高德地图',
          maxZoom: 18,
          minZoom: 9,
          subdomains: "1234"
        }).addTo(gaodeMap);
      },
      initTencent() {
        var tencentMap = L.map('tencentMap', {
          center: [39.916042, 116.390694],
          zoom: 14,
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        L.tileLayer('http://rt{s}.map.gtimg.com/realtimerender?z={z}&x={x}&y={y}&type=vector&style=0', {
          tms: true,
          attribution: '铿锵侠 &copy; 腾讯地图',
          maxZoom: 18,
          minZoom: 9,
          subdomains: "123"
        }).addTo(tencentMap);
      },
      initGoogle() {
        var googleMap = L.map('googleMap', {
          center: [39.916042, 116.390694],
          zoom: 14,
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        L.tileLayer('http://mt{s}.google.cn/vt/lyrs=m&hl=zh-CN&gl=cn&x={x}&y={y}&z={z}', {
          attribution: '铿锵侠 &copy; 谷歌地图',
          maxZoom: 18,
          minZoom: 9,
          subdomains: "123"
        }).addTo(googleMap);
      },
      initOpen() {
        var openMap = L.map('openMap', {
          center: [39.916042, 116.390694],
          zoom: 14,
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        L.tileLayer('http://{s}.tile.osm.org/{z}/{x}/{y}.png', {
          attribution: '铿锵侠 ©OpenStreetMap'
        }).addTo(openMap);
      },
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #maphome {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../../../static/background.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: hidden;
  }

  #headText {
    color: white;
    margin: 50px;
    font-size: 20px;
  }

  #mapcontent {
    display: flex;
    justify-content: center;
  }

  #home-cartoon {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

  #gaodeMap,
  #tencentMap,
  #googleMap,
  #openMap {
    margin: 0 auto;
    height: 750px;
    width: 1200px;
  }

</style>



