<template>
  <div id="maphome">
    <div id="headText">Tangram</div>
    <div id="mapcontent">
      <div id="map"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'mapUseTangram',
    data() {
      return {

      }
    },
    computed: {},
    mounted() {
      this.initLeaflet();
    },
    watch: {},
    methods: {
      initLeaflet() {
        var map = L.map('map', {
          fullscreenControl: {
            pseudoFullscreen: true
          }
        });
        var layer = Tangram.leafletLayer({
          scene: '../../../static/scene.yaml',
          attribution: "Tangram"
        });
        layer.addTo(map);
        map.setView([40.70531887544228, -74.00976419448853], 15);
      },
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #maphome {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-image: url(../../../static/background.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    overflow-y: hidden;
  }

  #headText {
    color: white;
    margin: 50px;
    font-size: 20px;
  }

  #mapcontent {
    display: flex;
    justify-content: center;
  }

  #home-cartoon {
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: -1;
  }

  #map {
    margin: 0 auto;
    height: 750px;
    width: 1200px;
  }

</style>



